            </p>
            <p>付款日：{{ item.paid_date }}</p>
            <p>付款方式：{{ item.payment_method }}</p>
          </td>
          <td>{{ item.message}}</td>
          <td>
            <div class="btn-group">
              <button type="button" class="btn btn-outline-primary">編輯</button>
              <button type="button" class="btn btn-outline-danger">刪除</button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      exampleOrder: {
        create_at: 1523539834,
        id: '-L9u2EUkQSoEmW7QzGLF',
        is_paid: true,
        message: '這是留言',
        paid_date: 1523539924,
        payment_method: 'credit_card',
        products: {
          L8nBrq8Ym4ARI1Kog4t: {
            id: 'L8nBrq8Ym4ARI1Kog4t',
            product_id: '-L8moRfPlDZZ2e-1ritQ',
            qty: '3'
          }
        },
        total: 100,
